/* NavBar Styles */
.navbar {
    display: flex;
    justify-content: space-between;
    background-color: var(--navbar-bg);
    color: var(--navbar-text);
  }
  
  .navbar.light {
    --navbar-bg: white;
    --navbar-text: black;
    --navbar-hover-bg: #f0f0f0;
    --navbar-hover-text: black;
  }
  
  .navbar.dark {
    --navbar-bg: black;
    --navbar-text: white;
    --navbar-hover-bg: #1a1a1a;
    --navbar-hover-text: white;
  }
  

  


  .ant-menu-horizontal > .ant-menu-item-selected {
    background-color: var(--navbar-hover-bg) !important;
  }
  
  .logout-button {
    margin-left: auto; 
  }
  
  .theme-toggle {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .theme-toggle span {
    margin-left: 8px;
  }
  
  .navbar.light .theme-toggle span {
    color: black;
  }
  
  .navbar.dark .theme-toggle span {
    color: white;
  }